/*Welcome*/

@media only screen and (max-width: 480px) {
#BackgroundOnlyWide{
    visibility: hidden;
}
#drawerElement{
    visibility: visible;
    margin-bottom: -120px;

}
#navbar{/*makes navbar invisivle in mobiles*/
    visibility: hidden;
    display: none;
}
#welcomeLogo{ /*sets logo at welcome page*/
    width: 80%;
    margin-top: 50px;
    margin-left: 10%;

}
#logo{ /*sets logo at pages*/
    width: 90%;
    margin-top: 80px;
    margin-left: auto;
    margin-right: auto;
    display: block;
}
#invisibleMobile{
    width: 0;
    height: 0;
    display: none;
}
#welcomeWhatsNew{
    width: 80%;
    margin-left: 10%;
}
#welcomeFeed{
    width: 85%;
}
#eventsPosts{
    width: 90%;
    margin-left: 10%;
}
#memberFilter{
    width:'100%'
}
}


/*větší než mobil*/
@media only screen and (min-width: 480px) {
#memberFilter{
width:'75%'
}
#eventsPosts{
    width: 60%;
    margin-left: 20%;
}
#drawerElement{
    visibility: hidden;
    display: none;
}
#welcomeLogo{
    width: 20%;
    margin-top: 180px;
}
#welcomeWhatsNew{
    width: 40%;
    margin-left: 30%;
    margin-top: 40px;
}
#invisibleWide{
    width: 0;
    height: 0;
    display: none;
}
#welcomeFeed{
    width: 50%;
}
#logo{
    width: 250px;

}
}